import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'userSearch'
})
export class UserSearchPipe implements PipeTransform {

  transform(allStudents: any[], searchName: string, categorySearch: string): any {

    if (categorySearch !== 'All') {
      return allStudents.filter(student =>  student.studentName.includes(searchName) && student.category === categorySearch);
    } else {
      return allStudents.filter(student =>  student.studentName.includes(searchName));
    }

  }
}
