import { NgModule } from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {StudentsBoardComponent} from './students-board/students-board.component';
import {OnboardingDetailsComponent} from './onboarding-details/onboarding-details.component';
import {HomeComponent} from './home/home.component';
import {LoginComponent} from './login/login.component';
import {AuthGuardService} from './auth-guard.service';

const routes: Routes = [
  { path: '', component: HomeComponent,
    children: [
      { path: '', redirectTo: 'onboard', pathMatch: 'full' },
      { path: 'onboard', component: OnboardingDetailsComponent, canActivate: [AuthGuardService] },
      { path: 'students', component: StudentsBoardComponent, canActivate: [AuthGuardService] }
    ]
  },
  {
    path: 'login', component: LoginComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
