import { Component, OnInit } from '@angular/core';
import {UserDataService} from '../user-data.service';

@Component({
  selector: 'app-students-board',
  templateUrl: './students-board.component.html',
  styleUrls: ['./students-board.component.css']
})
export class StudentsBoardComponent implements OnInit {

  searchStudent = '';
  categorySearch = 'All';
  studentsDisplayed = [];

  constructor(private userService: UserDataService) { }

  ngOnInit() {
    this.studentsDisplayed = this.userService.getAllUsers();
  }

  deleteUser(deleteUser): void {
    // console.log('in delete');
    const studentID = deleteUser.id;

    this.userService.deleteUser(studentID);
    this.studentsDisplayed = this.studentsDisplayed.filter(item => item.id !== studentID);
  }
}
