import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UserAuthService {
  loggedIn = 'loggedIn';
  authorizedUsers = [{
    userName: 'admin',
    password: 'password'
  }];

  lclStorage = window.localStorage;

  constructor() { }

  isLoggedIn(): boolean {
    if (this.lclStorage.getItem(this.loggedIn) === 'true') {
      return true;
    } else {
      return false;
    }
  }

  isAuthorizedUser(userInfo): boolean {
    // console.log(userInfo);
    if (userInfo === null || !(userInfo.hasOwnProperty('userName')) || !(userInfo.hasOwnProperty('password')) ) {
      return false;
    } else {
      for (const user of this.authorizedUsers) {
        // console.log('user' + user);
        if (user.userName === userInfo.userName && user.password === userInfo.password) {
          this.lclStorage.setItem(this.loggedIn, JSON.stringify(true));
          return true;
        }
      }
      return false;
    }
  }

}
