import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormArray, Validators} from '@angular/forms';
import {UserDataService} from '../user-data.service';
import {ActivatedRoute, Router} from '@angular/router';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {DocumentServiceService} from '../document-service.service';

@Component({
  selector: 'app-onboarding-details',
  templateUrl: './onboarding-details.component.html',
  styleUrls: ['./onboarding-details.component.css']
})
export class OnboardingDetailsComponent implements OnInit {
  curID = null;
  curCategory = '';
  editable = true;
  curDocuments = [];
  onboardForm = this.fb.group({
    studentName: ['', Validators.required],
    category: [''],
    documents: this.fb.array([
    ]),
    dob: ['', Validators.required],
    fatherName: ['', Validators.required],
    motherName: ['', Validators.required],
    lastClassScore: ['', Validators.required]
  });

  categories = ['Domestic', 'International'];

  state$: Observable<object>;

  constructor(private fb: FormBuilder, private userService: UserDataService,
              private activatedRoute: ActivatedRoute, private router: Router, private ds: DocumentServiceService) { }

  ngOnInit() {
    this.onChanges();

    this.state$ = this.activatedRoute.paramMap
      .pipe(map(() => window.history.state));

    this.state$.subscribe(value => { this.setFormState(value); });
  }

  setFormState(value): void {
    // console.log('In set ofrm state ' + JSON.stringify(value));
    if ('student' in value) {
      const formState = value.student;
      const navSource = value.source;
      this.curID = formState.id;
      // console.log('In set ofrm state ' + );
      this.onboardForm.patchValue(formState);
      if (navSource === 'view') {
        this.editable = false;
      }
    }
  }

  onSubmit(): void {
    this.userService.saveUserData(this.onboardForm.value, this.curID);
    this.router.navigateByUrl('/students');
  }

  onChanges(): void {
    this.onboardForm.get('category').valueChanges.subscribe(val => {
      this.curCategory = `${val}`;
      this.clearDocuments();
      if (this.curCategory === 'Domestic') {
        this.curDocuments = this.ds.neededDocumentsForDomestic();

        for (const document of this.curDocuments) {
          this.addDocuments(document.isMandatory);
        }
      }

      if (this.curCategory === 'International') {
        this.curDocuments = this.ds.neededDocumentsForInternational();

        for (const document of this.curDocuments) {
          this.addDocuments(document.isMandatory);
        }
      }
    });
  }

  get documents() {
    return this.onboardForm.get('documents') as FormArray;
  }

  addDocuments(isMandatory) {
    // console.log('inside add documents' + isMandatory);
    if (isMandatory) {
      // console.log('in mandatory');
      this.documents.push(this.fb.control(false, Validators.required));
    } else {
      this.documents.push(this.fb.control(false));
    }
  }

  clearDocuments() {
    while (0 !== this.documents.length) {
      this.documents.removeAt(0);
    }
  }
}
