import { Component, OnInit } from '@angular/core';
import {FormBuilder, Validators} from '@angular/forms';
import {UserAuthService} from '../user-auth.service';
import { Router} from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginForm = this.fb.group({
    userName: ['', Validators.required],
    password: ['', Validators.required]
  });

  constructor(private fb: FormBuilder, private userAuth: UserAuthService, private router: Router) { }

  ngOnInit() {
    if (this.userAuth.isLoggedIn()) {
      this.router.navigateByUrl('/onboard');
    }
  }

  onSubmit(): void {
    const userInfo = this.loginForm.value;
    if (this.userAuth.isAuthorizedUser(userInfo)) {
      // console.log('log succesful');
      this.router.navigateByUrl('/onboard');
    } else {

    }
  }

  resetFormValues(): void {
    this.loginForm.reset();
  }
}
